html {
	height: 100%;
	background-color: #333;

	&.f-menu-active {
		overflow: hidden;

		@media (min-width: 60em) {
			overflow: auto;
		}
	}
}

body {
	margin: 0;
	position: relative;
	.f-menu-active & {
		width:100%;
		height: 100%;
		//overflow:auto;
        /*
		@media (min-width: 60em) {
			overflow: auto;
		}*/
	}
}



p.sctnHdr{
    font-size: 3em;
    color: #990000;


}


.f-container {
	@include clearfix;
	@include border-box;
	position: relative;
	padding: 0 $f-margin;
	z-index: 0;
	min-height: 100vh;
    //Compensate menu bar
    //margin-top:50px;
    background-color: #333;


    .f-menu-active & {
        margin-left: 250px;
        @media screen and (max-width:1274px){ //Account for the menu
        	padding-left: 0em;
			padding-right: 0em;

        }
    }

    @media screen and (max-width:640px){
    	//margin-top:50px;
    }
}

//This is to style each "card"
.f-container > .f-item-group {
	background-color: #fff;
	padding: 3em;	
	border-radius: 4px;
	box-shadow: 0px 0.5em 0.5em #222;

}

@media screen and (max-width:1024px){

	.f-container{
		padding-left: 0em;
		padding-right: 0em;
	}
	.f-container > .f-item-group {
		padding-left: 1.5em;
		padding-right: 1.5em;
	}

}

.f-divider {
	border-bottom: 1px #990000 solid;
	box-shadow: 0px 3px 10px #777;
}

.f-notes-images{
	border: 1px #e3e3e3 solid;
	box-shadow: 0px 3px 3px #aaa;
}


.f-no-list > li {
	background: none;
}

.f-overview-main {
	color: #222;
	text-shadow: 0px 2px 3px #eee;

}
//------------------------end of overview

//-Search Box------
#searchAnswer a:visited{
	color: #118899;
}

