.f-item-group {
	@include clearfix;
	margin-top: 3em;
	margin-bottom: 3em;
	padding-bottom: 3em;
	//border-bottom: 1px solid map-get($colors, light);

	&:last-child {
		border-bottom: 0;
		margin-bottom: 0;
	}

	& ~ & {
		margin-top: 0;
	}

	& > & {
		padding-bottom: 1.5rem;
		margin-bottom: 1.5rem;
		border-bottom: 0;

		&:first-of-type {
			margin-top: 0;
		}

		&:last-child {
			margin-bottom: 0;
			padding-bottom: 0;
		}
	}
	//Main Overview Title section
	&.main {
		padding-left: 3em;
	}


	> img {
		max-width: 100%;
		box-shadow: 0px -2px 3px #333;
	}

}

.f-item-code {
	//margin-top: 2rem;
	border-radius: 4px;

	> .language-markup {
		box-shadow: 0px 1px 2px rgba(#aaa, 1) inset;
	}
}

.f-item-notes img {
	max-width: 100%;
	border: 1px solid #ddd;
}

.f-item-preview {
	@include clearfix;
	margin-top: 2rem;
}

/*
.f-item-border-bottom {
	border-bottom: 1px solid map-get($colors, light);
}*/

.f-item-heading-group {
	@include clearfix;
	margin-bottom: 1rem;
	vertical-align: middle;
	border-bottom: 1px #d9f1f4 solid;
	padding-bottom: 3px;
}

.f-item-heading {
	padding-top:0px;
	margin-top: 0;
	margin-bottom: 5px;

	display: inline-block;
	vertical-align: middle;
	line-height: 1;
	color: #444;
	font-weight: 600;



}


.f-item-controls {
	display: inline-block;
	vertical-align: middle;
	margin-left: 0.5rem;

	.f-control {
		width: 14px;
		height: 14px;
		display: block;
		float: left;

		&:hover {
			use {
				fill: map-get($colors, accent);
			}
		}

		use {
			fill: map-get($colors, light);
		}
	}

    .f-tag {
        use {
            fill: map-get($colors, accent);
        }
        display:none;
    }
}

.f-item-hidden {
	display: none;
}

.f-item-notes {
	//font-size: 0.875rem;
	padding: 2em 2em 0.8em 2em;
	background-color:#eee;
	border-radius: 4px;
}

.f-overview-item-card {
	background-color:#fff;
	padding: 1em;
}
