.f-controls {
	@include clearfix;
	@include border-box;

	.f-control {
		display: block;
		float: left;
		text-align: center;
		width: percentage(1/3);
		margin: 0;
		padding: 1rem 0;

		&.f-active {
			box-shadow: inset 0 3px 0 0 map-get($colors, accent);

			use {
				fill: map-get($colors, menu-text);
			}
		}

		svg {
			vertical-align: middle;

			use {
				fill: if($theme == $theme-dark, map-get($colors, normal), map-get($colors, light) );
			}
		}
	}
}

.f-control {
	@include border-box;
	display: inline-block;
	cursor: pointer;
	margin-left: 0.618rem;

	&:first-child {
		margin-left: 0;
	}

	svg {
		width: 14px;
		height: 14px;

		use {
			fill: map-get($colors, light);
		}
	}

    img {
        padding: 7px;
    }
    
}

.f-item-controls {
    img {
 		padding-top: 0;
    }
    span {
        margin-left: 5px;
    }
}

/*=====TAGGING ====*/

/*=== Title Tags ===*/
.f-item-tag {
    font-size:0.7em;
    position: relative;
    top: -0.3em;
    vertical-align: baseline;
    padding: 0.3em 0.9em;
    border-radius: 1em;
    text-transform: uppercase;
}

/* 01 --- New tag ---*/
.f-item-tag.new{
    background: #44bb55;
    color:#fff;
}

.f-item-tag.new::after{
    content: "New / Updated";
}

/* 02 --- Caution Tag ---*/
.f-item-tag.caution{
    background: #ff7700;
    color: #fff;
}

.f-item-tag.caution::after{
    content: "Beta";
}

/* 03 --- PFT Tag ---*/
.f-item-tag.pft{
    background: #555555;
    color: #fff;
}
.f-item-tag.pft::after{
    content: "Product Family Template";
}

/*=== Menu Tags ===*/
.f-menu-tag{
    font-size: 0.8em;
    position: relative;
    padding-left: .5em;
    font-style: normal;
    font-family: 'Material Icons';
    vertical-align: -0.1em;
    color: rgba(0,0,0,0); 
}

.f-menu-tag::after{
    content: "\E061";
}

/* 01 --- New tag ---*/
.f-menu-tag.new{
    color: #44bb55;
}

/* 02 --- Caution tag ---*/
.f-menu-tag.caution{
    color: #FF7F00;
}




/*======================*/


.f-control-bar {
	@include clearfix;
    //padding: 15px 30px; //IE8
	padding: 1em 2.5em; 
    position:fixed;
    top: 0;
    left: 0;
    z-index: 1000;
    width: 100%;
    background: #222; //IE8
    background: rgba(0,0,0, .7);

    
    a{
        color:#aaa;
        padding-left: 5px;
    }
    a:link{
    	color:#aaa;
    }

    a:visited{
        color:#aaa;
    }

    a:hover{
    	color: white;
    }
    
    .f-menu-active & {
        margin-left: 250px;
    }
    

    #f-control-bar-title{
    	display: initial;
        color: #ddd;
        text-shadow: 0px 2px 3px #555;
    	font-weight: 600;
    	text-transform: capitalize;

    	.f-menu-active & {

	    	@media (max-width: 640px) {
				display: none;
			}

		}
    }

    #f-control-bar-links{

    	.f-menu-active & {
    		display: none;
    	}

    }

    #f-search-bar{
    	display:initial;
    	position: absolute;
    	right: $f-margin - 0.5em;
    	top: 9px;
    	//box-shadow: -15px 0px 10px 0px rgba(#333, 0.5);

    	.f-menu-active & {
    		
    		right: 289px;


    		@media (max-width: 640px) {
				display: none;
			}
    	}
    }   

}

.f-control-bar-toggle {
    color:#fff !important;
    font-weight: 700;
    text-shadow: 0px 4px 3px rgba(0,0,0,0.4),
             0px 8px 13px rgba(0,0,0,0.1),
             0px 18px 23px rgba(0,0,0,0.1);
 }

.f-menu-toggle {
	cursor: pointer;
	vertical-align: middle;


	svg {
		display: block;
		float: left;
		margin-bottom: 2px;
        margin-left: 10px;
        margin-right: 5px;

	}
   
}

.f-notes-ie8{
